aac77515deacec82949b4e0685187&oe=609918CB"}})],1),_c('v-col',{attrs:{"cols":"12","md":"4"}},[_c('v-img',{staticClass:"ml-10 mr-10",attrs:{"height":"350px","src":"https://scontent-lga3-1.xx.fbcdn.net/v/t1.6435-9/169558885_121021776733168_5936852692700829422_n.jpg?_nc_cat=110&ccb=1-3&_nc_sid=8bfeb9&_nc_ohc=XljTMPDcklsAX8DZIkN&_nc_ht=scontent-lga3-1.xx&oh=60aaa31a2acefb9cfabb3b74f0441dab&oe=609856A3"}})],1)],1)],1),_c('v-tab-item',[_c('v-row',{staticClass:"intro"},[_c('v-col',[_c('v-card',{staticClass:"ml-1 mr-1 card",attrs:{"height":"100vh","color":"#D3D3D3"}},[_c('v-card-title',[_c('span',{staticClass:"title font-weight-bold"},[_vm._v("Brianna Caszatt")]),_c('v-card-actions',[_c('v-list-item',{staticClass:"grow"},[_c('v-list-item-avatar',{attrs:{"color":"grey darken-3"}},[_c('v-img',{staticClass:"elevation-6",attrs:{"alt":"","src":"https://mappingcemeteries.commons.gc.cuny.edu/wp-content/blogs.dir/16656/files/2021/04/Justice_Statue_Gravatar.png"}})],1)],1)],1)],1),_c('v-card-text',{staticClass:"font-weight-light"},[_vm._v(" Brianna hails from Michigan but has called Queens home for almost 13 years. She is a copy chief and copy editor by day and a graduate student in the MA in Digital Humanities (DH) program at the CUNY Graduate Center by night. Joining the DH community has definitely been her best pandemic decision. And buying more sweatpants. DH and sweatpants: the two best pandemic decisions by far. Mapping Cemeteries brings together her interests (some old and some new-found) in social justice, memory studies, infrastructure, and mapping. The original idea for the project was hers, but what the project has become is so much more than she ever could have expected, and it is all thanks to the creative and collaborative energy of the entire team. As project manager, she has created a group space for everyones internal communications and workflow needs, she sets the meeting agendas and keeps notes during each meeting, and she makes sure blockers are addressed and goals are met. She is also responsible for the researching and data collection of a cemetery that was redeveloped and turned into a public park: City Hall Park. ")])],1)],1),_c('v-col',[_c('v-card',{staticClass:"ml-1 mr-1",attrs:{"height":"100vh","color":"#D3D3D3"}},[_c('v-card-title',[_c('span',{staticClass:"title font-weight-bold"},[_vm._v("Lisa Kofod")]),_c('v-card-actions',[_c('v-list-item',{staticClass:"grow"},[_c('v-list-item-avatar',{attrs:{"color":"grey darken-3"}},[_c('v-spacer'),_c('v-img',{staticClass:"elevation-6",attrs:{"alt":"","src":"https://upload.wikimedia.org/wikipedia/commons/7/7a/War_Memorial_Swords_Shield_Close-Up.png"}})],1)],1)],1)],1),_c('v-card-text',{staticClass:"font-weight-light"},[_vm._v(" Lisa is a writer and producer, they are also a digital humanities student working toward their Master of Arts in the Digital Humanities at the Graduate Center of the City University of New York (CUNY). A life-long New Yorker, they were drawn to this project because of their love of New York City history and their fascination with how we choose to remember those who came before. How do we determine who deserves to be remembered? What do those remembrances look like? How have these remembrances changed over time? Their primary roles in this project were site design and testing. This included working with the developer on which platform to use and what frameworks to apply, creating best practices for the incorporation of digital assets, and testing the site as it was developed. Their secondary roles were in documentation and research. Documentation included tracking conversations during the development stage and encouraging the team away from scope creep. Research responsibilities included working on technical options and scholarly research on a set of war memorials within Manhattan. ")])],1)],1),_c('v-col',[_c('v-card',{staticClass:"ml-1 mr-1",attrs:{"height":"100vh","color":"#D3D3D3"}},[_c('v-card-title',[_c('span',{staticClass:"title font-weight-bold"},[_vm._v("Asma N. ")]),_c('v-spacer'),_c('v-card-actions',[_c('v-list-item',{staticClass:"grow"},[_c('v-list-item-avatar',{attrs:{"color":"grey darken-3"}},[_c('v-spacer'),_c('v-img',{staticClass:"elevation-6",attrs:{"alt":"","src":"https://images.unsplash.com/photo-1606170300294-84f3213babe3?ixid=MXwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHw%3D&ixlib=rb-1.2.1&auto=format&fit=crop&w=1934&q=80"}})],1)],1)],1)],1),_c('v-card-text',{staticClass:"font-weight-light"},[_vm._v(" Asma (ahs-ma) N. is a Brooklyn native, Digital Humanist, Black feminist, and a graduate student at The Graduate Center, CUNY. She uses audio/technology to discourse her broader philosophical research interests in gender, social ontology, and digital humanities to develop original content in public scholarship. Asma cares about creatively transposing whats meta about experience into accessible language and technology alike. Her other central topics include beauty politics, digital space and identity, and sound studies  all in relationship to Black women and individuals who are the priority in her work. Asma has a strong background in research and advocacy, having maintained a handful of fellowships across reproductive health, leadership, and diplomacy during and post undergrad at the University of Maryland, College Park. Shes also held appointments as a coordinator of education and training for diversity initiatives, and educational data management in the sites of D.C./Maryland and New York City. Asma is 1/5th of the team, Mapping Cemeteries, which is a digital humanist timeline project that explores the relationship between identity and death across four distinct cemeteries in New York City. She contributes her research skills in language, methodology, and analysis, as well as project design, strategy, and audiovisual modulation. Most of the above says more about her professional abilities and less about who is Asma. Somewhere at the core are the matters of the head and the heart that speak to love, wellness, and Halloween (her favorite time of year). ")])],1)],1),_c('v-col',[_c('v-card',{staticClass:"ml-1 mr-1",attrs:{"height":"100vh","color":"#D3D3D3"}},[_c('v-card-title',[_c('span',{staticClass:"title font-weight-bold"},[_vm._v("Nadia El Mouldi")]),_c('v-card-actions',[_c('v-list-item',{staticClass:"grow"},[_c('v-list-item-avatar',{attrs:{"color":"grey darken-3"}},[_c('v-img',{staticClass:"elevation-6",attrs:{"alt":"","src":"http://3.bp.blogspot.com/-_Sqq5JpNPAc/Ulyvd9u7zzI/AAAAAAAAIMM/NXY2CuWWuVQ/s1600/peck.jpg"}})],1)],1)],1)],1),_c('v-card-text',{staticClass:"font-weight-light"},[_vm._v(" Nadia is a software engineer and a graduate student in the Master of Arts in Digital Humanities program at CUNY. She has graduated with a BA in Computer Science and Statistics, helping her to develop skills in software design and development with a focus on ethical practices and critical problem solving. This has sparked her interest in developing Digital Humanities projects that incorporate data visualization and web application spanning a range of topics. As a team member in the Mapping Cemeteries Project, Nadia is responsible for the web development and mapping effort. This includes framework and tool selection, as well as implementation and testing. She is also responsible for a portion of the research and data collection, pertaining to historic cemeteries in New York City. ")])],1)],1),_c('v-col',[_c('v-card',{staticClass:"ml-1 mr-1",attrs:{"height":"100vh","color":"#D3D3D3"}},[_c('v-card-title',[_c('span',{staticClass:"title font-weight-bold"},[_vm._v("Lane Vineyard")]),_c('v-card-actions',[_c('v-list-item',{staticClass:"grow"},[_c('v-list-item-avatar',{attrs:{"color":"grey darken-3"}},[_c('v-img',{staticClass:"elevation-6",attrs:{"alt":"","src":"https://upload.wikimedia.org/wikipedia/commons/a/a0/Coffin_%281%29.png"}})],1)],1)],1)],1),_c('v-card-text',{staticClass:"font-weight-light"},[_vm._v(" lane vineyard is a first-year student at the CUNY Graduate Center currently pursuing a masters in Digital Humanities. They currently hold a B.A. in Linguistics, with minors in Spanish and Mandarin, from Middle Tennessee State University. Though they have some educational background in the arts and humanities, they have only recently begun delving into the field of Digital Humanities. Their research interests include queer culture and history, digital mapping tools, design, and studying the intersection of language and identity with DH tools. lanes primary role for the project is outreach, which entails the creation and maintenance of an online presence for the project. Additionally, they do primary research for one of the cemeteries showcased in the project, which is a cemetery that was repurposed, rediscovered, and now exists again as both a cemetery and national monument ")])],1)],1)],1)],1),_c('v-tab-item',{staticClass:"form",attrs:{"height":"1000px"}},[_c('form',{staticClass:"mr-15 ml-15",attrs:{"height":"500px"}},[_c('v-text-field',{attrs:{"error-messages":_vm.nameErrors,"label":"Name","required":""},on:{"input":function($event){return _vm.$v.name.$touch()},"blur":function($event){return _vm.$v.name.$touch()}},model:{value:(_vm.name),callback:function ($$v) {_vm.name=$$v},expression:"name"}}),_c('v-text-field',{attrs:{"error-messages":_vm.emailErrors,"label":"E-mail","required":""},on:{"input":function($event){return _vm.$v.email.$touch()},"blur":function($event){return _vm.$v.email.$touch()}},model:{value:(_vm.email),callback:function ($$v) {_vm.email=$$v},expression:"email"}}),_c('v-text-field',{attrs:{"dense":"","label":"message","required":""},on:{"input":function($event){return _vm.$v.message.$touch()},"blur":function($event){return _vm.$v.message.$touch()}},model:{value:(_vm.message),callback:function ($$v) {_vm.message=$$v},expression:"message"}}),_c('v-btn',{staticClass:"mr-4",on:{"click":_vm.submit}},[_vm._v(" submit ")]),_c('v-btn',{on:{"click":_vm.clear}},[_vm._v(" clear ")])],1)])],1)],1)],1)}
